li {
  height: 5rem;
  position: relative;
}

.todo-item {
  width: 97%;
  display: flex;
  padding: 0.5rem;
  justify-content: space-between;
  align-items: center;
  border-radius: 1rem;
}

.display{
  background: white;
  transform : translateX(0%);
  transition-timing-function: ease-in;
  transition: 0.2s;
}

.delete-warning, .display {
  position: absolute;
}

.checked {
  text-decoration: line-through;
}

.active {
  background: #828e9f;
}

.deleted {
  transform : translateX(-120%);
}

.title {
  height: 60px;
  line-height: 60px;  /* center text vertically */
}

/** BUttons **/
.complete-delete {
  height: 2rem;
  background: none;
  border-radius: 1rem;
  cursor: pointer;
  padding: 0.5rem 1rem;
}

.complete-delete:hover {
  background: #d8dff7;
}

.delete-icon {
  width: 2rem;
  background: none;
  border: none;
