.form-container {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.todo-form-btn-container {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  gap: 2rem;
}

.form-category-picker {
  margin-top: 0.5rem;
  height: 3.5rem;
}

.form-category-text {
  margin-top: 0.5rem;
  resize: vertical;
  height: 7rem;
  font-size: larger !important;
  padding-top: 0.5rem;
}

.form-category-text,
.form-category-picker {
  background: white;
  opacity: 0.4;
  border-width: thin;
  border-radius: 0.75rem;
  font-size: medium;
  text-indent: 0.5rem;
  box-shadow: rgba(27, 31, 35, 0.04) 0px 1px 0px,rgba(255, 255, 255, 0.25) 0px 1px 0px inset;
}

.form-category-text:hover,
.form-category-picker:hover {
  opacity: 1;
  /* box-shadow: rgba(0, 0, 0, 0.95) 0px 25px 20px -15px; */
  box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px;
}

.form-category-picker-label, .form-category-text-label {
  margin-top: 1rem;
  font-weight: bold;
}

.todo-form-btn {
  width: 100%;
  height: 3.5rem;
  border-radius: 1rem;
  color: white;
  font-size: large;
}

.btn-cancel {
  background: #0e1f54;
  opacity: 0.75;
}

.btn-save {
  background: #8a57ff;
  opacity: 0.75;
}

.btn-cancel:hover,
.btn-save:hover {
  opacity: 1;
}
